<!-- eslint-disable -->
<template>
  <div class="slideshow-wrapper">
    <div id="slideshow-home">
      <a
        class="slide-a"
        v-for="s in slides"
        :href="s.url"
        :key="s.id"
        target="_blank"
        :data-slide-id="s.id"
        :class="{'active': isActive}"
      >
        <div
          class="slide"
          :class="{'blue': s.id % 2 === 0}"
          :style="{'background-image': 'url(' + s.static_img + ')'}"
        >
          <div class="svg-container" v-if="!s.has_image">
            <img-base v-if="s.file == 'chair'" img-name="chair" viewBox="0 0 330 340">
              <img-chair />
            </img-base>

            <img-base v-if="s.file == 'children'" img-name="children" viewBox="0 0 410 340">
              <img-children />
            </img-base>

            <img-base v-if="s.file == 'moon'" img-name="moon" viewBox="0 0 510 340">
              <img-moon />
            </img-base>

            <img-base v-if="s.file == 'frame'" img-name="frame" viewBox="0 0 620 340">
              <img-frame />
            </img-base>
          </div>

          <div class="copy" :class="{'blue': s.id % 2 === 0}" v-if="!s.has_image">
            <div class="title" :class="{'white': s.id % 2 === 0}">{{ s.title }}</div>
            <div class="line"></div>
            <div class="text" :class="{'white': s.id % 2 === 0}" v-html="s.text"></div>
            <div class="btn white">
              <div class="txt">{{ s.btn_txt }}</div>
            </div>
          </div>
        </div>
      </a>

      <!-- <div class="arrows-wrapper">
        <div class="arrows">
          <div class="arrow prev" @click="plusSlides(-1)"></div>
          <div class="arrow next" @click="plusSlides(1)"></div>
        </div>
      </div>-->
    </div>
    <div class="indicators">
      <div
        class="ind"
        :class="{'active': isActive}"
        v-for="s in slides"
        :key="s.id"
        @click="currentSlide(s.id)"
        :data-slide-id="s.id"
      ></div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";
import chair from "../../assets/illustrations/final/chair.svg";
import chair2 from "../../assets/illustrations/final/chair.svg";
import ImgBase from "../ImgBase";
import ImgChair from "../illustrations/ImgChair";
import ImgChildren from "../illustrations/ImgChildren";
import ImgMoon from "../illustrations/ImgMoon";
import ImgFrame from "../illustrations/ImgFrame";

export default {
  components: {
    ImgBase,
    ImgChair,
    ImgChildren,
    ImgMoon,
    ImgFrame,
  },
  data() {
    return {
      slideIndex: 1,
      dataLoaded: false,
      isActive: true,
      xDown: null,
      yDown: null,
      intervals: [],
      slides: [
        {
          id: "",
          title: "",
          text: "",
          btn_txt: "",
          file: "",
          url: "",
          static_img: "",
          has_image: false,
        },
      ],
    };
  },
  methods: {
    drawLines() {
      $(".nodules").addClass("start");
      $(".main-path").addClass("start");
      $(".main-path-fill").addClass("start");
    },
    deleteLines() {
      $(".nodules").removeClass("start");
      $(".main-path").removeClass("start");
      $(".main-path-fill").removeClass("start");
    },
    playAnim() {
      var vm = this;

      var slider = document.querySelector("#slideshow-home");
      var i = setInterval(() => {
        vm.deleteLines();
        vm.prevSlides(-1);
        var value = -0.25 * (vm.slideIndex - 1) * 100;
        slider.style.transform = "translateX(" + value + "%)";
      }, 8000);

      vm.intervals.push(i);
    },
    showSlide(n) {
      var vm = this;
      var i;
      var slides = document.querySelectorAll("#slideshow-home .slide-a");
      var dots = document.querySelectorAll(".indicators .ind");

      if (n > slides.length) {
        vm.slideIndex = 1;
      }
      if (n < 1) {
        vm.slideIndex = slides.length;
      }
      for (i = 0; i < slides.length; i++) {
        slides[i].className = "slide-a";
      }
      for (i = 0; i < dots.length; i++) {
        dots[i].className = dots[i].className.replace(" active", "");
        vm.isActive = false;
      }
      slides[vm.slideIndex - 1].className = "slide-a active";
      dots[vm.slideIndex - 1].className += " active";
      vm.isActive = true;
    },
    prevSlides(n) {
      var vm = this;
      vm.showSlide((vm.slideIndex -= n));
      vm.intervals.forEach(clearInterval);
      setTimeout(() => {
        vm.drawLines();
        vm.playAnim();
      }, 200);
    },
    plusSlides(n) {
      var vm = this;
      vm.showSlide((vm.slideIndex += n));
      vm.intervals.forEach(clearInterval);
      setTimeout(() => {
        vm.drawLines();
        vm.playAnim();
      }, 200);
    },
    currentSlide(n) {
      var vm = this;
      vm.showSlide((vm.slideIndex = n));
      vm.deleteLines();
      // clearInterval(auto);
      vm.intervals.forEach(clearInterval);

      var slider = document.querySelector("#slideshow-home");
      slider.removeEventListener("touchstart", vm.handleTouchStart, false);
      slider.removeEventListener("touchmove", vm.handleTouchMove, false);

      var value = -0.25 * (vm.slideIndex - 1) * 100;
      slider.style.transform = "translateX(" + value + "%)";

      setTimeout(() => {
        vm.drawLines();

        setTimeout(() => {
          slider.addEventListener("touchstart", vm.handleTouchStart, false);
          slider.addEventListener("touchmove", vm.handleTouchMove, false);

          vm.playAnim();
        }, 1000);
      }, 200);
    },
    getTouches(evt) {
      return evt.touches || evt.originalEvent.touches;
    },
    handleTouchStart(evt) {
      var vm = this;
      const firstTouch = vm.getTouches(evt)[0];
      vm.xDown = firstTouch.clientX;
      vm.yDown = firstTouch.clientY;
    },
    handleTouchMove(evt) {
      var vm = this;

      if (!vm.xDown || !vm.yDown) {
        return;
      }

      var xUp = evt.touches[0].clientX;
      var yUp = evt.touches[0].clientY;

      var xDiff = vm.xDown - xUp;
      var yDiff = vm.yDown - yUp;

      var active = document.querySelector("#slideshow-home .slide-a.active");
      var activeDot = document.querySelector(".indicators .ind.active");
      var id = active.getAttribute("data-slide-id");
      var slider = document.querySelector("#slideshow-home");

      if (Math.abs(xDiff) > Math.abs(yDiff)) {
        /*most significant*/
        if (xDiff > 0) {
          /* left swipe */
          vm.deleteLines();
          vm.plusSlides(1);
          var value = -0.25 * (vm.slideIndex - 1) * 100;
          slider.style.transform = "translateX(" + value + "%)";
          clearInterval();
        } else {
          /* right swipe */
          vm.deleteLines();
          vm.prevSlides(1);
          var value = -0.25 * (vm.slideIndex - 1) * 100;
          slider.style.transform = "translateX(" + value + "%)";
          clearInterval();
        }
      }

      vm.xDown = null;
      vm.yDown = null;
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;
    vm.deleteLines();

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getSlideshow.php")
      .then((resp) => {
        const data = resp.data;
        var slides = [];

        map.call(data, function (d) {
          if (d.static_img == null) {
            slides.push({
              id: d.id,
              title: d.title,
              text: d.text,
              btn_txt: d.cta_txt,
              file: d.img,
              url: d.link,
              static_img: "",
              has_image: false,
            });
          } else {
            var src = d.static_img.split("uploads");
            slides.push({
              id: d.id,
              title: d.title,
              text: d.text,
              btn_txt: d.cta_txt,
              file: d.img,
              url: d.link,
              static_img: "https://beta.anacarolinapereira.pt/uploads" + src[1],
              has_image: true,
            });
          }
        });

        vm.$data.slides = slides;
      })
      .then(() => {
        // vm.drawLines();
        setTimeout(() => {
          $("#slideshow-home").addClass("not-hidden");
          var slideArray = [];
          $(".slide-a").each(function () {
            slideArray.push($(this));
          });

          // console.log(Object.keys(slideArray).length);
          $("#slideshow-home").css(
            "width",
            100 * Object.keys(slideArray).length + "%"
          );
          vm.dataLoaded = true;

          if (vm.dataLoaded == true) {
            var slider = document.querySelector("#slideshow-home");
            var dots = document.querySelector(".indicators");
            dots.style.opacity = 1;
            dots.style.pointerEvents = "all";

            // var timer = setInterval(() => {
            //   vm.deleteLines();
            //   vm.plusSlides(vm.slideIndex);
            //   var value = -0.25 * (vm.slideIndex - 1) * 100;
            //   slider.style.transform = "translateX(" + value + "%)";
            // }, 8000);
            vm.currentSlide(vm.slideIndex);

            slider.addEventListener("touchstart", vm.handleTouchStart, false);
            slider.addEventListener("touchmove", vm.handleTouchMove, false);
          }
        }, 0);
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: rgba(11, 171, 197, 0.6);

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(65);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.slideshow-wrapper {
  background-color: $white;
  height: 93vh;
  width: 100vw;
  position: absolute;
  z-index: 99;
  overflow: hidden;

  #slideshow-home {
    height: 100%;
    position: relative;
    opacity: 0;
    transition: 0.5s;
    display: flex;

    &.not-hidden {
      opacity: 1;
    }

    a {
      width: 100%;
      height: 100%;
      display: block;

      .slide {
        width: 100%;
        height: 100%;
        display: flex;
        background-position: 90% 50%;
        background-color: $white;
        align-items: center;
        background-repeat: no-repeat;
        // position: absolute;
        transition: 1s;
        // background-size: 30%;
        pointer-events: all;
        overflow: hidden;
        justify-content: space-around;
        background-size: cover;
        flex-direction: row-reverse;

        &.blue {
          // background-color: #79c7d3;
          background-color: #f3f3f3;
        }

        @media only screen and (min-width: 992px) and (max-width: 1199px) {
          // justify-content: center;
        }

        @media only screen and (min-width: 768px) and (max-width: 991px) {
          background-position: top;
          // background-size: 70% 50%;
          // align-items: flex-start;
          flex-direction: column;
        }

        @media only screen and (min-width: 600px) and (max-width: 767px) {
          background-position: top;
          // background-size: 50% 50%;
          // align-items: flex-start;
          flex-direction: column;
        }

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          background-position: top;
          // background-size: 70% 50%;
          // align-items: flex-start;
          flex-direction: column;
        }

        .svg-container {
          // width: 50%;
          height: 100%;
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;

          @media only screen and (min-width: 768px) and (max-width: 991px) {
            // width: 100%;
            height: 50%;
            align-items: flex-start;
            margin-top: 5%;
          }

          @media only screen and (min-width: 600px) and (max-width: 767px) {
            // width: 100%;
            height: 50%;
            align-items: flex-start;
            margin-top: 5%;
          }

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            // width: 100%;
            height: 50%;
            align-items: flex-start;
            margin-top: -15%;
          }

          svg {
            width: pxToEm(700);
            height: pxToEm(700);
            transform: scale(0.8);

            @media only screen and (min-width: 1200px) and (max-width: 1920px) {
              transform: scale(1);
            }

            @media only screen and (min-width: 992px) and (max-width: 1199px) {
              transform: scale(1);
            }

            @media only screen and (min-width: 768px) and (max-width: 991px) {
              transform: scale(1);
            }

            @media only screen and (min-width: 600px) and (max-width: 767px) {
              transform: scale(0.7);
            }

            @media only screen and (min-width: 300px) and (max-width: 599px) {
              transform: scale(0.65);
              top: pxToEm(-100);
              position: absolute;
            }
          }
        }

        .copy {
          position: relative;
          display: flex;
          flex-direction: column;
          // width: 100%;
          max-width: pxToEm(900);
          height: pxToEm(500);
          padding: pxToEm(40);
          justify-content: space-between;
          // position: absolute;
          // left: 12.5vw;

          &.blue {
            // background-color: #79c7d380;
            // background-color: #f3f3f37a;
          }

          @media only screen and (max-width: 1200px) {
            align-items: center;
            text-align: center;
          }

          // @media only screen and (min-width: 768px) and (max-width: 991px) {
          //   left: 50%;
          //   transform: translateX(-50%);
          //   bottom: pxToEm(120);
          // }

          // @media only screen and (min-width: 600px) and (max-width: 767px) {
          //   left: 50%;
          //   transform: translateX(-50%);
          //   bottom: pxToEm(120);
          // }

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            // left: 50%;
            // transform: translateX(-50%);
            // bottom: pxToEm(80);
            // background-color: rgba(255, 255, 255, 0.8);
            margin-top: -75%;
            height: pxToEm(400);
          }

          .title {
            color: $turquiose;
            font-size: pxToEm(48);
            font-family: "Dalmatins";
            font-weight: bold;

            &.white {
              // color: #fafafa;
            }

            @media only screen and (min-width: 300px) and (max-width: 599px) {
              font-size: pxToEm(38);
            }
          }

          .line {
            width: 30%;
            height: pxToEm(1);
            background-color: $turquiose;
            margin-top: pxToEm(-50);

            @media only screen and (min-width: 300px) and (max-width: 599px) {
              margin-top: pxToEm(-50);
            }
          }

          .text {
            margin-top: pxToEm(10);
            color: $dk-gray-3;
            font-size: pxToEm(28);
            font-family: "OS-Regular";
            line-height: pxToEm(35);

            &.white {
              // color: #fafafa;
            }

            @media only screen and (min-width: 300px) and (max-width: 599px) {
              padding: 0 pxToEm(10);
            }
          }

          .btn {
            width: pxToEm(200);
            height: pxToEm(60);

            .txt {
              font-family: "OS-Bold";
              font-size: pxToEm(20);
            }
          }
        }
      }
    }
  }

  .indicators {
    opacity: 0;
    pointer-events: none;
    display: flex;
    width: pxToEm(130);
    justify-content: space-between;
    position: absolute;
    bottom: pxToEm(200);
    left: 50%;
    transform: translateX(-50%);

    @media screen and (max-width: 599px) {
      bottom: pxToEm(100);
    }

    .ind {
      border-radius: $circle;
      width: pxToEm(22);
      height: pxToEm(22);
      background-color: $white;
      border: pxToEm(1) solid #707070;
      cursor: pointer;

      &.active {
        background-color: #707070;
      }
    }
  }
}
</style>



